import { hot } from 'react-hot-loader/root';
import React from 'react'
import ReactDOM from 'react-dom'

const ComfirmPure = ({onCancel, onComfirm}) => (
  <div style={{width: '100vw',position: 'fixed', left: 0,top: '0', height: '100vh', background: 'rgba(55,55,55,0.6)', zIndex: '100'}}>
    <div style={{position: 'absolute', background: '#fff', transform: 'translate(-50%, -50%)', left: '50%', top: '50%', width: '400px'}}>
      <header>title</header>
      <section>content</section>
      <footer>
        <button onClick={onCancel}>cancle</button>
        <button onClick={onComfirm}>comfirm</button>
      </footer>
    </div>
  </div>
)
let index = 0
let __ROOT_DOM_KEY__='__ROOT_DOM_KEY__'
const getDom = () => {
  const key = __ROOT_DOM_KEY__ + index
  const el = document.createElement('div')
  el.id = key
  document.body.appendChild(el)
  index ++
  return el
}
ComfirmPure.onConfirm = async (props) => {
  const el = getDom()

  const onCancel = () => {
    props.onCancel()
    ReactDOM.unmountComponentAtNode(el)
    el.parentNode.removeChild(el)
  }
  const onComfirm = () => {
    props.onComfirm()
    ReactDOM.unmountComponentAtNode(el)
    el.parentNode.removeChild(el)
  }

  ReactDOM.render(<ComfirmPure {...props} onCancel={onCancel} onComfirm={onComfirm}/>, el)
}

const 中文方法 = () => {
  console.log('%c 中文方法 =========>', 'color:#722ed1;', )
}
export default hot(() => {
  return (
    <div>
      <div onClick={() => ComfirmPure.onConfirm({
        onCancel: () => {console.log('%c onComfirm =========>', 'color:#722ed1;', )},
        onComfirm: () => {console.log('%c onComfirm =========>', 'color:#722ed1;', )},
      })}>showConfirm1</div>
      <div onClick={() => 中文方法()}>中文方法</div>
    </div>
  )
})