import React, { useState } from 'react'



export default class DragSort extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      value: [
        {
            content: 'div1',
            code: '01',
            sort: 0,
        },
        {
            content: 'div2',
            code: '02',
            sort: 1
        },
        {
            content: 'div3',
            code: '03',
            sort: 2
        },
        {
            content: 'div5',
            code: '05',
            sort: 5
        },
        {
            content: 'div4',
            code: '04',
            sort: 4
        }]
    }
  }

      // 拖动事件
      domdrugstart(index, code, ee) {
        ee.dataTransfer.setData("code", code);
        ee.dataTransfer.setData("index", index);
    }
    // 拖动后鼠标进入另一个可接受区域
    dragenter(ee) {
        ee.target.style.border = '2px dashed #008dff';
        ee.target.style.boxShadow = '0 0 8px rgba(30, 144, 255, 0.8)';
    }
    // a拖到b，离开b的时候触发
    dragleave(ee) {
        ee.target.style.border = '1px solid grey';
        ee.target.style.boxShadow = '';
    }
    // 对象排序
    compare(key) {
        return (obj1, obj2) => {
            if (obj1[key] < obj2[key]) {
                return -1;
            } else if (obj1[key] > obj2[key]) {
                return 1;
            }
            return 0
        }
    }
    // 当一个元素或是选中的文字被拖拽释放到一个有效的释放目标位置时
    drop(index, data, sortKey, ee) {
        ee.preventDefault();
        const code = ee.dataTransfer.getData("code");
        const sort = ee.dataTransfer.getData("index");
        console.log('%c ee =========>', 'color:#722ed1;', code)
        if (sort < index) {
            data.map(item => {
                if (item.code === code) {
                    item[sortKey] = index;
                } else if (item[sortKey] > sort && item[sortKey] < index + 1) {
                    item[sortKey]--;
                }
                return item;
            });
        } else {
            data.map(item => {
                if (item.code === code) {
                    item[sortKey] = index;
                } else if (item[sortKey] > index - 1 && item[sortKey] < sort) {
                    item[sortKey]++;
                }
                return item;
            });
        }
        this.setState({
          value: data
        })
        // this.props.onChange(data)
    }
    allowDrop(ee) {
        ee.preventDefault();
    }


      // 生成拖拽组件
      createDraggleComponent(data, sortKey, style, uId) {
        return data.map((item, index) => {
            return (
                <div
                    // className={styles.content}
                    key={item.code}
                    draggable={true}
                    onDragEnter={this.dragenter.bind(this)}
                    onDragLeave={this.dragleave.bind(this)}
                    onDragStart={this.domdrugstart.bind(this, index, item.code)}
                    onDrop={this.drop.bind(this, index, data, sortKey)}
                    onDragOver={this.allowDrop.bind(this)}
                    style={{ ...style }}>{item.content}</div>
            )
        })
    }
    render() {
      let sortKey = 'sort'
      let style = {}
        return (
            <el>
                <div style={{ display: 'flex', flexDirection: 'row' }}>
                    {this.createDraggleComponent(this.state.value, sortKey, style)}
                </div>
            </el>
        )
    }
}
