import React from 'react'
import PropTypes from 'prop-types'
import {connect} from 'react-redux'
import {toggleTodo, addTodo, addTodoAsync, addTodoLeading, addTodoLatest} from '../../../store/storeSimpleWithSaga/actions'
import { useState } from 'react'

const Todo = ({ ...props }) => {
  const [text, setText] = useState('')
  console.log('%c props =========>', 'color:#722ed1;', props)
  return (
    <div>
      {
        props.todos && props.todos.map(el => (
          <li>
            {el.text}
          </li>
        ))
      }
      <input type="text" value={text} onChange={val => {
        console.log('%c val =========>', 'color:#722ed1;', val.target.value)
        setText(val.target.value)
      }} />
      <button onClick={() => {props.addItem(text)}}>add</button>
      <button onClick={() => {props.addAsync(text)}}>addAsync</button>
      <button onClick={() => {props.addLeading(text); setText(t => t + 'w')}}>addLeading</button>
      <button onClick={() => {props.addLatest(text); setText(t => t + 'w')}}>addLatest</button>
    </div>
  )
} 

Todo.propTypes = {
}

const mapState = (state) => {
  console.log('%c state =========>', 'color:#722ed1;', state)
  return {
    todos: state.todos,
  }
}
const mapDispatch = (dispatch) => {
  console.log('%c dispatch =========>', 'color:#722ed1;', dispatch)
  return {
    addItem: (text) => dispatch(addTodo(text)),
    changeTodoStatus: index => dispatch(toggleTodo(index)),
    addAsync: text => dispatch(addTodoAsync(text)),
    addLeading: text => dispatch(addTodoLeading(text)),
    addLatest: text => dispatch(addTodoLatest(text)),
  }
} 

export default connect(mapState, mapDispatch)(Todo)