import React, {createRef, useState} from 'react';
import {Editor, EditorState, RichUtils,CompositeDecorator, Modifier, convertToRaw, AtomicBlockUtils} from 'draft-js';
import * as convert from 'draft-convert'
import stateToHTML from './utils/stateToHTML'

// 处理inlinestyle  如果将customStyleMap 中的key移除，再加新的  一般是处理同一类的style  如 color： red|blue|gray etc.
const dealSameTypeStyle = ({ getEditorState }, styleKey, customStyleMap) => {
  let editorState = getEditorState()
  const selection = editorState.getSelection()
  // 参考 https://github.com/facebook/draft-js/tree/master/examples/draft-0-10-0/color
  // 删除所有的颜色相关active
  const nextContentState = Object.keys(customStyleMap).reduce((contentState, color) => {
    return Modifier.removeInlineStyle(contentState, selection, color)
  }, editorState.getCurrentContent())

  let nextEditorState = EditorState.push(editorState, nextContentState, 'change-inline-style')

  const currentStyle = editorState.getCurrentInlineStyle()
  console.log('%c currentStyle =========>', 'color:#722ed1;', currentStyle)
  //  将其他的样式绑定到当前元素
  if (selection.isCollapsed()) {
    nextEditorState = currentStyle.reduce((state, color) => {
      return RichUtils.toggleInlineStyle(state, color)
    }, nextEditorState)
  }

  // If the color is being toggled on, apply it.
  if (!currentStyle.has(styleKey)) {
    console.log('%c styleKey =========>', 'color:#722ed1;', styleKey)
    nextEditorState = RichUtils.toggleInlineStyle(nextEditorState, styleKey)
  }
  return nextEditorState
}

function findTagEntities(contentBlock, callback, contentState) {
  contentBlock.findEntityRanges(
    (character) => {
      const entityKey = character.getEntity();
      return (
        entityKey !== null &&
        contentState.getEntity(entityKey).getType() === 'TextAlign'
      );
    },
    (...args) => { console.log('%c Tag args =========>', 'color:#722ed1;', args); callback(...args)} 
  );
}

const TextAlign = (props) => {
  console.log('%c props =========>', 'color:#722ed1;', props)
  const {style} = props.contentState.getEntity(props.entityKey).getData();
  console.log('%c style =========>', 'color:#722ed1;', style)
  return (
    <span className="tag" style={{...style, display: 'block'}}>
      {props.children }
    </span>
  );
};

const decorator = new CompositeDecorator([
  {
    strategy: findTagEntities,
    component: TextAlign,
  },
]);

const fontStyleMap = {
  'font-size-12': {
    fontSize: '12px',
  },
}

const colorStyleMap = {
  'color-red': {
    color: 'red'
  }
}

export const styleMap = {
  ...fontStyleMap,
  ...colorStyleMap,
}


let initValue = '<p><span style="font-size:12px"><span style="text-align:right, display: block">11111111</span></span></p>'
// 参考链接 https://www.npmjs.com/package/draft-convert#converttohtml
const convertFromHTML = (value) => convert.convertFromHTML({
  htmlToStyle: (nodeName, node, currentStyle) => {
      if (nodeName === 'span' && node.style.fontSize === '12px') {
          return currentStyle.add('font-size-12');
      } else {
          return currentStyle;
      }
  },
  htmlToEntity: (nodeName, node, createEntity, ) => {
      if (nodeName === 'span' && node.style.textAlign) {
        let textAlign = node.style.textAlign
          return createEntity(
              'TextAlign',
              'MUTABLE',
              {style: {
                textAlign,
                display: 'block',
              },
              nodeName: 'span'  
            }
          )
      }
  },
})(value);

const TextalignDraft = () => {
  const [value, setValue] = useState(initValue)
  let [editorState, seteditorState] = useState(() =>
  value
      ? EditorState.createWithContent(convertFromHTML(value), decorator)
      : EditorState.createEmpty(decorator)
  )
  const addRight = (e, textAlign) => {
    e.preventDefault();
    const contentState = editorState.getCurrentContent();
    const contentStateWithEntity = contentState.createEntity(
      'TextAlign',
      'MUTABLE',
      {style: {textAlign, display: 'block'}}
    );
    const entityKey = contentStateWithEntity.getLastCreatedEntityKey();
    const newEditorState = EditorState.set(editorState, { currentContent: contentStateWithEntity });
    console.log('%c addRight =========>', 'color:#722ed1;', textAlign)
    seteditorState(RichUtils.toggleLink(
      newEditorState,
      newEditorState.getSelection(),
      entityKey
    ));
  }

  const changefont = (key) => {
    const getEditorState = () => editorState
    let nextEditorState = dealSameTypeStyle({ getEditorState }, key, fontStyleMap)
    console.log('%c nextEditorState =========>', 'color:#722ed1;', nextEditorState)
    seteditorState(nextEditorState)

  }
  const changecolor = (key) => {
    const getEditorState = () => editorState
    let nextEditorState = dealSameTypeStyle({ getEditorState }, key, colorStyleMap)
    console.log('%c nextEditorState =========>', 'color:#722ed1;', nextEditorState)
    seteditorState(nextEditorState)

  }
  const onChange = (editorState) => {
    seteditorState(editorState)
  }

  const getHtml = () => {
    const html = convert.convertToHTML({
      entityToHTML: (entity, originalText) => {
        if (entity.type === 'TextAlign') {
          console.log('%c TextAlign =========>', 'color:#722ed1;',  )
          return <span style={entity.data.style}>
                  { originalText }
                </span>
        }
        return originalText;
      },
      styleToHTML: (style, ...args) => {
        console.log('%c style =========>', 'color:#722ed1;', style, ...args)
        if (styleMap[style]) {
          return <span style={styleMap[style]} />;
        }
      },
      blockToHTML: (block) => {
      },
    })(editorState.getCurrentContent());
    setValue(html)
    console.log('%c html =========>', 'color:#722ed1;', html)
  }


  return <div style={{width: '600px'}}>
    <p style={{padding: '24px'}}>文字对齐方式的draft-js + inlineStyle   --- RichUtils.toggleLink</p>
    <button  onMouseDown={(e) => addRight(e, 'right')}>addRight</button>
    <button  onMouseDown={(e) => addRight(e, 'center')}>addcenter</button>
    <button  onMouseDown={(e) => addRight(e, 'left')}>addleft</button>

    <button  onMouseDown={(e) => changefont('font-size-12')}>changefont-12</button>
    <button  onMouseDown={(e) => changecolor('color-red')}>change color-red</button>
    <br/>
    <button onClick={() => getHtml()}> get HTML</button>
    <button onClick={() => {
      let html = stateToHTML(editorState.getCurrentContent())
      setValue(html)
      console.log('%c html =========>', 'color:#722ed1;', html)
    }}> get HTML by draft-js-export-html</button>
    <button onClick={() => {seteditorState(
      EditorState.createWithContent(convertFromHTML(value), decorator)
    )}}> set HTML</button>

    <button  onClick={() => {
      console.log('%c editorState =========>', 'color:#722ed1;', editorState.getBlockTree())
    }}> log something</button>
    <Editor
      editorState={editorState}
      customStyleMap={styleMap}
      onChange={onChange}
      placeholder="Please input here..."
    />
    <p dangerouslySetInnerHTML={{ __html: value }}></p>
    <p> <span style={{color: 'red'}}>2222<p>11111</p></span>  </p>
  </div>
}

export default TextalignDraft