import React from 'react'
import {connect} from 'react-redux'
import {sagaAddAction} from '../../../store/storeWithRematch/saga'

const Counter = ({count,
  increment,
  color,
  sagaAdd2,
  changeColor,
  sagaAdd,
  incrementAsync}) => {
  return (
    <div>
      Counter
      <p style={{color: color}}>{count}</p>
      <button onClick={() => {increment(1)}}>add</button>
      <button onClick={() => {incrementAsync(1)}}>addAsnyc</button>
      <button onClick={() => {sagaAdd(4)}}>sagaAdd</button>
      <button onClick={() => {sagaAdd2(6)}}>sagaAdd2</button>
      <button onClick={() => {changeColor(color === 'red' ? 'blue' : 'red')}}>changeColor</button>
    </div>
  )
}


const mapState = (state) => {
  console.log('%c state =========>', 'color:#722ed1;', state)
  return {
    count: state.count.count,
    color: state.count.color,
  }
}
const mapDispatch = (dispatch) => {
  console.log('%c dispatch =========>', 'color:#722ed1;', dispatch.sagaAdd)
  return {
    increment: dispatch.count.increment,
    incrementAsync: dispatch.count.incrementAsync,
    sagaAdd2: dispatch.count.sagaAdd,
    changeColor: dispatch.count.changeColor,
    sagaAdd: (num) => {
      console.log('%c num =========>', 'color:#722ed1;', num)
      dispatch({ type: sagaAddAction, payload: num })
    },
  }
}
export default connect(mapState, mapDispatch)(Counter)

