// https://savokiss.com/tech/webpack-dynamic-require.html

const GetImages = () => {
  const getImages = () =>{
    let imagList = []
    let images = require.context('./images', true, /.[png|jpg|gif]$/)
    images.keys().forEach(el => {
      console.log('%c el =========>', 'color:#722ed1;', images(el))
      imagList.push(images(el).default)
    })
    var ImageListContainer = document.getElementById("ImageList");
    imagList.forEach(el =>{

      const img = document.createElement("img");
      img.setAttribute("class","img");
      img.src = el;
      ImageListContainer.appendChild(img);
    })

  }

  return (
    <div>
      <button onClick={getImages}>获取文件中的图片</button>
      <div id="ImageList"></div>
    </div>
  )
}

export default GetImages