import styled from 'styled-components';

const UIModalMask = styled.div`
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(55, 55, 55, 0.6);
    height: 100%;
    z-index: 999;
    transition: all 0.4s ease;
    opacity: 1;
`
const UIModalArea = styled.div`
    position: absolute;
    outline: 0;
    left: 50%;
    transform: translate(-50%, -50%);
    top: 50%;
    z-index: 9999;
    box-shadow: 0 2px 8px 0 rgb(0 0 0 / 14%);
    background: #ffffff;
`


export const Modal = ({children, show}) => {
  return(
    <div style={{display: !show && 'none'}}>
      <UIModalMask></UIModalMask>
      <UIModalArea>
        {children}
      </UIModalArea>
    </div>
  )
}