import Page2 from '../pages/page2/dragon'
import GetImages from '../pages/page2/dragon/GetImages'
import Page2_2 from '../pages/page2/Page22'
import draft from '../pages/page2/draftjs'
// import draft from '../pages/page2/draftjs/draftImage'
import linkdraft from '../pages/page2/draftjs/linkDraft'
import BeautifulDragThrough from '../pages/page2/beautifulDragThrough'
import switchRoutes from './switchRoutes'
import _ from 'lodash'


export const routerPath = '/admin/page2'

export const bar = {
  name: '一些功能',
  path: routerPath,
}

export const pages = {
  page2: {
    title: '泡泡龙',
    path: `${routerPath}/1`,
    component: Page2,
  },
  GetImages: {
    title: '获取文件夹中的图片',
    path: `${routerPath}/GetImages`,
    component: GetImages,
  },
  page2_2: {
    title: '拖拽功能',
    path: `${routerPath}/2`,
    component: Page2_2,
  },
  draft: {
    title: 'draft-富文本',
    path: `${routerPath}/draft`,
    component: draft,
  },
  linkdraft: {
    title: 'linkDraft-富文本',
    path: `${routerPath}/linkdraft`,
    component: linkdraft,
  },
  BeautifulDragThrough: {
    title: '拖拽穿梭',
    path: `${routerPath}/BeautifulDragThrough`,
    component: BeautifulDragThrough,
  }
}

export const menus = [
  pages.page2,
  pages.page2_2,
  pages.draft,
  pages.GetImages,
  // pages.linkdraft,
  pages.BeautifulDragThrough,
]

export const Router = switchRoutes({ routes: _.values(pages), menus, routerPath })


