import React, {useCallback,useState, useRef, useLayoutEffect, useEffect} from 'react'

function CallBackTest() {
  const [text, updateText] = useState('');

  const textRef = useRef();

  useLayoutEffect(() => {
    textRef.current = text; // useLayoutEffect 等同于didUpdate将 text 写入到 ref
  });

  const handleSubmit = useCallback(() => {
    console.log('%c handleSubmit =========>', 'color:#722ed1;', text)
    console.log('%c textRef =========>', 'color:#722ed1;', textRef.current)
  }, []); // 不传text时，text获取的是初始值, 如果传入text依赖,handleSubmit会一直更新,相当于没用usecallback
  const handleClick = () => {
    console.log('%c handleClick =========>', 'color:#722ed1;', )
  }
  return (
    <>
      <input value={text} onChange={(e) => updateText(e.target.value)} />
      <button onClick={handleSubmit}>handleSubmit</button>
      <button onClick={handleClick}>handleClick</button>
    </>
  );
}

function Sixunhuan() {
  let count = 0;

  function App() {
    const [val, setVal] = useState("");

    function getData() {
      setTimeout(() => {
        setVal("new data " + count);
        count++;
      }, 500);
    }

    return <Child val={val} getData={getData} />;
  }

  function Child({val, getData}) {
    useEffect(() => {
      getData();
    }, [getData]);

    return <div>{val}</div>;
  }
  return <App/>
}

function MemoAndUsecallback() {
  const Child = React.memo(function({name, val, onChange}) {
    console.log('render...', name);
    
    return <input value={val} onChange={onChange} />;
  });
  
  function App() {
    const [val1, setVal1] = useState('');
    const [val2, setVal2] = useState('');
  
    const onChange1 = useCallback( evt => {
      setVal1(evt.target.value);
    }, []);
  
    const onChange2 = useCallback( evt => {
      setVal2(evt.target.value);
    }, []);
    // const onChange1 = evt => {
    //   setVal1(evt.target.value);
    // }
  
    // const onChange2 = evt => {
    //   setVal2(evt.target.value);
    // }
  
    return (
    <>
      <Child name="1" val={val1} onChange={onChange1}/>
      <Child name="2" val={val2} onChange={onChange2}/>
    </>
    );
  }
  return <App/>
}

export default () => (
  <div>
    <div className="title">useCallBack</div>
    <br/>
    <article>
      <p>不太理解这段话 </p>
      useCallback 的作用在于利用 memoize 减少无效的 re-render
      <p>React.memo 和 React.useCallback 一定记得需要配对使用，缺了一个都可能导致性能不升反“降”</p>
    </article>
    <article>
      <p>useCallBack, 其实是给方法做缓存而已， 减少定义，否则每次渲染都会定义一个function</p>
      <p>useCallBack 方法是一个闭包， 他如果要访问外部的state只能通过引入他的依赖，否则只能拿到初始值， 那么要怎么办呢</p>
      <p>使用useRef， 因为ref在render的时候是不变的， 所以，将值存在于ref中就可以获取了</p>
      <p>ref作为依赖引入， 如果不作为依赖呢， 也能访问到</p>
      <p>callback作为依赖是eslint的要求</p>
      <br/>
      <CallBackTest/>
      <br/>
      <title>你不知道的 useCallback</title>
      <p>强行使用usecallBack性能只会更差, --- 性能 <a href="https://segmentfault.com/a/1190000020108840">你不知道的 useCallback</a></p>
      <p>useEffect可能会引起死循环， 子组件根据父组件的方法改变数据</p>
      <Sixunhuan/>
      <p>
      真正有助于性能改善的，有 2 种场景：
      <p>
        函数定义时需要进行大量运算， 这种场景极少
      </p>
      <p>需要比较引用的场景，如上文提到的useEffect，又或者是配合React.Memo使用：</p>
      <p>配合memo使用的时候， 根据props是否修改来判断组件是否render</p>
        <br/>
        <MemoAndUsecallback/>
      </p>
      <p>react.memo 是根据组件的props的变化来判断是否渲染组件， usecallback是将函数包成一个不变的值，useMeo是将一切引用对象包成一个不变的值</p>
    </article>
  </div>
)