import React from 'react'
import { Redirect, Route, Switch, Link } from 'react-router-dom'
import { useLocation } from 'react-router-dom'
import { getbars, getAllModules, getMenus } from './modules'
import * as ModuleLoader from './modules'
import {getFirstRoute} from './getFirstRoute'

function Bar(props) {
  return <div>
    {
      props.data.map(el => <span style={{marginLeft: '10px'}}>
        <Link to={el.path}>{el.name}</Link>
      </span>)
    }
  </div>
}


function Left(props) {
  let location = useLocation()

  let menus = []

  if (!props.Routers) return null
  for (const router of props.Routers) {
    if (
      router.routerPath &&
      router.menus &&
      location.pathname.slice(0, router.routerPath.length) === router.routerPath
    ) {
      menus = ModuleLoader.getMenus(router.routerPath)
    }
  }
  console.log('%c menus =========>', 'color:#722ed1;', menus)
  return <ul>
    {
      menus.map(el => <li>
        <Link to={el.path}>{el.title}</Link>
      </li>)
    }
  </ul>
}

function Roothome() {

  const Routers = getAllModules()
  const bars = getbars()
  const firstRoute = getFirstRoute(Routers)
  return(
    <div>
      <Bar data={ bars }/>
      <div style={{display: 'flex'}}>
        <div style={{flex: '0 0 200px'}}>
          <Left Routers={Routers}/>
        </div>
        <div style={{height: 'calc(100vh - 30px)', width: 'calc(100vw - 200px)', overflow: 'auto'}}>
          <Switch>
              {Routers.map(router => (
                <Route key={router.routerPath} component={router.Router} path={router.routerPath} />
                ))}
                { firstRoute &&  <Redirect to={firstRoute.path}></Redirect>}
          </Switch>
        </div>
      </div>
    </div>
  )
}

export default Roothome