import React, {createRef} from 'react';
import ReactDOM from 'react-dom';
import {Editor, EditorState, RichUtils,CompositeDecorator, Modifier, convertToRaw, AtomicBlockUtils} from 'draft-js';
import 'draft-js/dist/Draft.css';
import './draft.css'
import { Link as LinkRouter } from 'react-router-dom'
import ColorfulEditorExample from './ColorSelect';
import TextAlignBlock from './TextAlignBlock';

const Media = props => {
  if (!props.block.getEntityAt(0)) return null;

  const entity = props.contentState.getEntity(props.block.getEntityAt(0));
  const { src, name } = entity.getData();
  const type = entity.getType();

  let media = null;

  if (type === 'link') {
    const key = props.block.getKey();

    media = <a target="_blank"  href={src}>{name}</a>;
  }
  if (type === 'image') {
    const key = props.block.getKey();

    media = <img
      src={src} 
      blockKey={key}
      handleDelete={() => {
        props.blockProps.deleteImage(props.block);
      }}
    />;
  }

  return media;
};

function findLinkEntities(contentBlock, callback, contentState) {

  contentBlock.findEntityRanges(
    (character) => {
      const entityKey = character.getEntity();
      console.log('%c contentState.getEntity(entityKey).getType() =========>', 'color:#722ed1;', entityKey && contentState.getEntity(entityKey).getType())
      return (
        entityKey !== null &&
        contentState.getEntity(entityKey).getType() === 'LINK'
      );
    },
    (...args) => { console.log('%c LINK args =========>', 'color:#722ed1;', args); callback(...args)} 
  );
}
function findTagEntities(contentBlock, callback, contentState) {
  contentBlock.findEntityRanges(
    (character) => {
      const entityKey = character.getEntity();
      return (
        entityKey !== null &&
        contentState.getEntity(entityKey).getType() === 'Tag'
      );
    },
    (...args) => { console.log('%c Tag args =========>', 'color:#722ed1;', args); callback(...args)} 
  );
}
const Link = (props) => {
  const {url} = props.contentState.getEntity(props.entityKey).getData();
  return (
    <a href={url}>
      {props.children }
    </a>
  );
};
const Tag = (props) => {
  return (
    <p className="tag" style={{textAlign: 'right'}}>
      {props.children }
    </p>
  );
};
export const mediaBlockRenderer = (block, { deleteImage }) => {

  if (block.getType() === 'atomic') {
    return {
      component: Media,
      editable: false,
      props: {
        deleteImage
      },
    };
  }
};
class MyEditor extends React.Component {
  constructor(props) {
    const decorator = new CompositeDecorator([
      {
        strategy: findLinkEntities,
        component: Link,
      },
      {
        strategy: findTagEntities,
        component: Tag,
      },
    ]);
    super(props);
    this.state = {editorState: EditorState.createEmpty(decorator)};
    this.onChange = editorState => this.setState({editorState});
    this.handleKeyCommand = this.handleKeyCommand.bind(this);
  }
  handleKeyCommand(command, editorState) {
    const newState = RichUtils.handleKeyCommand(editorState, command);

    if (newState) {
      this.onChange(newState);
      return 'handled';
    }

    return 'not-handled';
  }
  _onBoldClick() {
    this.onChange(RichUtils.toggleInlineStyle(this.state.editorState, 'BOLD'));
  }
  _onBoldClick() {
    this.onChange(RichUtils.toggleInlineStyle(this.state.editorState, 'BOLD'));
  }
  addImage = src => {
    const editorState = this.state.editorState;
    const contentState = editorState.getCurrentContent();
    const contentStateWithEntity = contentState.createEntity(
      'image',
      'IMMUTABLE',
      { src: 'https://p9-juejin.byteimg.com/tos-cn-i-k3u1fbpfcp/9c49fde575e048aaa8b1d4102d514b0d~tplv-k3u1fbpfcp-watermark.image' }
    );
    const entityKey = contentStateWithEntity.getLastCreatedEntityKey();
    console.log('%c entityKey =========>', 'color:#722ed1;', entityKey)
    const newEditorState = EditorState.set(
      editorState,
      { currentContent: contentStateWithEntity },
      'create-entity'
    );

    this.setState({
      editorState: AtomicBlockUtils.insertAtomicBlock(newEditorState, entityKey, ' ')
    });
  }
  addLink = e => {
    e.preventDefault();
    const {editorState, urlValue} = this.state;
    const contentState = editorState.getCurrentContent();
    const contentStateWithEntity = contentState.createEntity(
      'LINK',
      'MUTABLE',
      {url: 'www.baidu.com'}
    );
    const entityKey = contentStateWithEntity.getLastCreatedEntityKey();
    const newEditorState = EditorState.set(editorState, { currentContent: contentStateWithEntity });
    this.setState({
      editorState: RichUtils.toggleLink(
        newEditorState,
        newEditorState.getSelection(),
        entityKey
      ),
      showURLInput: false,
      urlValue: '',
    });
  }
  addRight = e => {
    e.preventDefault();
    const {editorState, urlValue} = this.state;
    const contentState = editorState.getCurrentContent();
    const contentStateWithEntity = contentState.createEntity(
      'Tag',
      'MUTABLE',
      {url: 'www.baidu.com'}
    );
    const entityKey = contentStateWithEntity.getLastCreatedEntityKey();
    const newEditorState = EditorState.set(editorState, { currentContent: contentStateWithEntity });
    console.log('%c addRight =========>', 'color:#722ed1;', )
    this.setState({
      editorState: RichUtils.toggleLink(
        newEditorState,
        newEditorState.getSelection(),
        entityKey
      ),
    });
  }
  // 颜色选择器
    // 参考 https://github.com/facebook/draft-js/tree/master/examples/draft-0-10-0/color
  // 删除所有的颜色相关active
  render() {
    return (
      <div style={{width: '100px', height: '100px'}}>
        <p>
          <LinkRouter target="_self" to="/admin/page2/linkdraft">
            linkDraft
          </LinkRouter>
        </p>
        <br/>
        <button onClick={this._onBoldClick.bind(this)}>Bold</button>
        <button onClick={this._onBoldClick.bind(this)}></button>
        <button onClick={this.addImage.bind(this)}>addImage</button>
        <button  onMouseDown={this.addLink.bind(this)}>addLink</button>
        <button  onMouseDown={this.addRight.bind(this)}>addRight</button>
        <Editor
            editorState={this.state.editorState}
            handleKeyCommand={this.handleKeyCommand}
            onChange={this.onChange}
            placeholder="Please input here..."
            ref={this.setDomEditorRef}
            plugins={this.plugins}
            blockRendererFn={(block) => mediaBlockRenderer(block, { 
              deleteImage: this.deleteImage
            })}
          />
          <div style={{margin: '30px 0'}}>ces</div>
          <ColorfulEditorExample/>
          <TextAlignBlock/>
      </div>
    );
  }
}

export default MyEditor