import useMouseMove from "../../../myHooks/useMounseMove";
import {useCallback, useState} from 'react'

function TestMounseHook() {
  const [position, setposition] = useState({ top: 0 , left: 400})
  // let {onMouseDown} = useMouseMove({
  //   mouseMove: useCallback(
  //     ({ dx, dy, event }) => {
  //       let newPosition = {
  //         top: position.top + dy,
  //         left: position.left + dx,
  //       }
  //       setposition(old => newPosition)
  //       console.log('%c dx, dy, event =========>', 'color:#722ed1;', dx, dy, event)
  //     },
  //   )
  // }) // 不行， 因为mouseMove的时候，会有一个递增效果 top = oy + 1y + 2y + 3y   实际我们需要的是 top = oy + 3y 而已
  let mousedown = useCallback(({getEventData}) => {
    let data = getEventData()
    data.top = position.top
    data.left = position.left
  })
  let {onMouseDown} = useMouseMove({
    mouseDown: mousedown,
    mouseMove: useCallback(
      ({ dx, dy, getEventData }) => {
        let data = getEventData()

        let newPosition = {
          top: data.top + dy,
          left: data.left + dx,
        }
        setposition(newPosition)
        // console.log('%c dx, dy, event =========>', 'color:#722ed1;', dx, dy,)
      },
    )
  })
  return (
    <div style={{position: 'relative', width: '100vw', height: '50vh'}}>
      <p style={{width: '200px', height: '200px', background: 'blue', marginBottom: '100px'}} onMouseDown={(e, data) => {
        console.log('%c e =========>', 'color:#722ed1;', e, data)
      }}>not onMouseDown Hook</p>
      <p style={{width: '200px', height: '200px', background: 'red',position: 'absolute',
            top: `${position.top}px`, 
            left: `${position.left}px`, 
      }} onMouseDown={onMouseDown}>onMouseDown hook</p>
    </div>
  )
}
// 所以diy hook 更像是一个mixin，可以将复用逻辑的代码块提取出来
const useInput = (arg1) => {
  const [text, setText] = useState(arg1 || '')
  const onchange = (val) => {
    setText(val.currentTarget.value)
  }
  return [text, onchange]
}

function TestInputHooks() {
  const [text, setText] = useInput('')
  return (
    <div>
      input diy hook
      <input value={text} onChange={(val) => {setText(val)}}/>
    </div>
  )
}

export default function TestDiyHook() {
 return(
   <div>
     <TestMounseHook/>
     <TestInputHooks/>
   </div>
 )
}
