import Page1 from '../pages/page1'
import Page1_2 from '../pages/page1/Page12'
import PageRedux from '../pages/page1/redux/todoList'  
import reduxRemact from '../pages/page1/redux/reduxRemact'  
import hooks from '../pages/page1/learnHooks'  
import diyHooks from '../pages/page1/learnHooks/diyHooks'  
import { Route, Switch, Redirect } from 'react-router-dom'
import switchRoutes from './switchRoutes'
import _ from 'lodash'
import { hot } from 'react-hot-loader/root'


export const routerPath = '/admin/page1'

export const bar = {
  name: 'react基础学习',
  path: routerPath,
}

export const pages = {
  page1: {
    title: 'react-useCallBack',
    path: `${routerPath}/1`,
    component: hot(() => <Page1/>),
  },
  page1_2: {
    title: '弹窗1111',
    path: `${routerPath}/2`,
    component: Page1_2,
  },
  PageRedux: {
    title: 'reduxTodo',
    path: `${routerPath}/redux`,
    component: PageRedux,
  },
  reduxRemact: {
    title: 'Remach学习',
    path: `${routerPath}/reduxRemact`,
    component: reduxRemact,
  },
  hooks: {
    title: 'hooks学习',
    path: `${routerPath}/hooks`,
    component: hooks,
  },
  diyHooks: {
    title: 'diyHooks学习',
    path: `${routerPath}/diyHooks`,
    component: diyHooks,
  },
}

export const menus = [
  pages.page1,
  pages.page1_2,
  pages.reduxRemact,
  pages.hooks,
  pages.diyHooks,
  // pages.PageRedux,
  // 打开注释的话src\index.js对应的redux切换到storeSimpleWithSaga

]


export const Router = switchRoutes({ routes: _.values(pages), menus, routerPath })
