import { stateToHTML } from "draft-js-export-html";
import _ from 'lodash'
import { styleMap } from '../TextAlignBlock'

let options = {
  inlineStyleFn: (styles) => {
    
    let style = {}
    console.log('%c styles =========>', 'color:#722ed1;', styles)
    styles.forEach((value) =>  {
      if (styleMap[value]) {
        style = {
          ...style,
          ...styleMap[value]
        }
      }
      console.log('%c value =========>', 'color:#722ed1;', value)
    });
    if (!_.isEmpty(style))
    return {
      element: 'span',
      style: {
        ...style
      },
    };
  },
  entityStyleFn: (entity) => {
    const entityType = entity.get('type')
    if (entityType === 'TextAlign') {
      const data = entity.getData();

      return {
        element: 'span',
        attributes: {
          // src: data.src,
        },
        style: {
          ...data.style
          // Put styles here...
        },
      };
    }
  },
};

export default function (contentState) {

  let html = stateToHTML(contentState, options);
  return html
}