/**
 * 先搜索routes（二级菜单）再搜索一级菜单
 * @param router 模块
 * @param routeMap 路由权限
 */
export const getFirstRouteFromModule = ({ menus }) => {
  for (const menu of menus) {
    if (!menu) continue
    if (menu.routes) {
      const routes = menu.routes
      if (routes.length > 0 && routes[0].component) return routes[0]
    } else {
      if (menu.component) return menu
    }
  }
  return null
}

/**
 * 先搜索routes（二级菜单）再搜索一级菜单
 * @param Routers
 * @param routeMap
 */
export const getFirstRoute = (Routers, routeMap) => {
  for (const router of Routers) {
    const ret = getFirstRouteFromModule(router, routeMap)
    if (ret) return ret
  }
  return null
}
