import { put, takeEvery, all,delay, takeLeading, takeLatest } from 'redux-saga/effects'
let sagaName = 'count'
export const sagaAddAction = `${sagaName}/sagaAdd`

function* sagaAdd() {
  yield takeEvery(sagaAddAction, function* (action) {
    console.log('%c action =========>', 'color:#722ed1;', action)  // action
    yield delay(1000)
    yield put( {type: 'count/increment', payload: action.payload })
  })
}
// function* sagaAdd() {
//   takeEvery(sagaAddAction, function* (payload) {
//     console.log('%c payload =========>', 'color:#722ed1;', payload)
//     put('count/increment', {count: })
//   })
// }

export default function* rootSaga(){
  yield all([
    sagaAdd()
  ])
}