let __modules = []
let __bars = []
let __menus = {}

export const load = urls => {
  // 获取routers下所有的xxxxxRouter.js
  const modules = require.context('./', true, /Router\.js$/)
  __modules = modules
      .keys()
      .map(key => modules(key))
  console.log('%c modules =========>', 'color:#722ed1;', __modules)
  resolveModules()
}

export function getAllModules() {
  return __modules
}

export function getbars() {
  return __bars
}

export function getMenus(routerPath) {
  return __menus[routerPath]
}

const resolveModules = () => {
  __modules.forEach(module => {
    const { bar, menus, routerPath } = module
    __bars.push(bar)
    __menus[routerPath] = menus
  })
}