/*
 * action 类型
 */

export const ADD_TODO = 'ADD_TODO';
export const ADD_TODO_ASYNC = 'ADD_TODO_ASYNC';
export const TOGGLE_TODO = 'TOGGLE_TODO'
export const ADD_TODO_Leading = 'ADD_TODO_Leading'
export const ADD_TODO_LATE = 'ADD_TODO_LATE'
export const SET_VISIBILITY_FILTER = 'SET_VISIBILITY_FILTER'

/*
 * 其它的常量
 */

export const VisibilityFilters = {
  SHOW_ALL: 'SHOW_ALL',
  SHOW_COMPLETED: 'SHOW_COMPLETED',
  SHOW_ACTIVE: 'SHOW_ACTIVE'
}

/*
 * action 创建函数
 */

export function addTodo(text) {
  return { type: ADD_TODO, text }
}
export function addTodoAsync(text) {
  return { type: ADD_TODO_ASYNC, text }
}
export function addTodoLeading(text) {
  return { type: ADD_TODO_Leading, text }
}
export function addTodoLatest(text) {
  return { type: ADD_TODO_LATE, text }
}

export function toggleTodo(index) {
  return { type: TOGGLE_TODO, index }
}

export function setVisibilityFilter(filter) {
  return { type: SET_VISIBILITY_FILTER, filter }
}