import React, {useEffect, useState} from 'react'
import { Modal } from './Modal'
import './dragon.css'
import dragon from './dragon.png'
// import dragon from './dragon2.png'
import bubbleSrc from './bubble.png'
import girl2 from './images/girl2.jpg'
import img1 from './images/img1.gif'
import img2 from './images/img2.png'
import img3 from './images/img3.png'
import img4 from './images/img4.jpg'
import img5 from './images/img5.jpg'
import img6 from './images/img6.png'
import img7 from './images/img7.png'

let images = [girl2, img1,
  img2,
  img3,
  img4,
  img5,
  img6,
  img7]

const getRandomImage = () => {
  let index = Math.floor(Math.random() * 8)
  return images[index]
}

var gap = 6;
const Draw = () => {
  const [popImg, setPopImg] = useState('')

  const initImage = () => {
    const ctx2 = document.getElementById("drawCanvus").getContext("2d");
    ctx2.fillStyle = "#000";
    ctx2.fillRect(0,0,1200,1200)
    let canvas = document.getElementById("canvas");
    var ctx = canvas.getContext("2d");

    var image = new Image();
    image.src = dragon;
    image.onload = function(){
            canvas.width = image.width;
            canvas.height = image.height;

            ctx.drawImage(image,0,0);
    }
    let imageData = ctx.getImageData(0,0,image.width,image.height).data;

    ctx.fillStyle = "#ffffff";
    ctx.fillRect(0,0,image.width,image.height);

  }
  useEffect(() => {
    initImage()
  }, [])


  const handleGetdianzheng = () =>{
    let canvas = document.getElementById("canvas");
    let image = document.getElementById("image");
    var ctx = canvas.getContext("2d");
    var imageData = ctx.getImageData(0,0,image.width,image.height).data; 
    /* imageData = {
      data: [],
      width: 100,
      height: 100,
    } data是从左到右的像素点的集合， 每四个为一个像素点  rgba(0,0,0,255) */
    console.log('%c imageData =========>', 'color:#722ed1;', imageData)
    ctx.fillStyle = "#ffffff";
    ctx.fillRect(0,0,image.width,image.height); // 前两个为开始， 后两个为大小

    

    for (var h = 0; h < image.height; h+=gap) {
        for(var w = 0; w < image.width; w+=gap){
                var position = (image.width * h + w) * 4;
                var r = imageData[position], g = imageData[position + 1], b = imageData[position + 2];

                if(r+g+b==0){
                  console.log('%c w,h,4,4 =========>', 'color:#722ed1;', w,h,position)
                        ctx.fillStyle = "#000";
                        ctx.fillRect(w,h,4,4);
                    }
        }
    }

  }

  const createCanVusPaoPao = () =>{
    let image = document.getElementById("image");
    let canvas = document.getElementById("canvas");
    var ctx = canvas.getContext("2d");
    var imageData = ctx.getImageData(0,0,image.width,image.height).data; 
    /* imageData = {
      data: [],
      width: 100,
      height: 100,
    } data是从左到右的像素点的集合， 每四个为一个像素点  rgba(0,0,0,255) */
    console.log('%c imageData =========>', 'color:#722ed1;', imageData)

    var imgObj = new Image();
    imgObj.src = bubbleSrc;
    const ctx2 = document.getElementById("drawCanvus").getContext("2d");
    ctx2.fillStyle = "#666";
    ctx2.fillRect(0,0,1200,1200)
    var dragonScale = 3;
    imgObj.onload = function(){
      console.log('%c imgObj =========>', 'color:#722ed1;', imgObj, imageData)
      for (var h = 0; h < image.height; h+=gap) {
          for(var w = 0; w < image.width; w+=gap){
                  var position = (image.width * h + w) * 4;
                  var r = imageData[position], g = imageData[position + 1], b = imageData[position + 2];
  
                  if(r+g+b==0){
                    var bubbleSize = Math.random()*10+20;
                    let left = w*dragonScale-bubbleSize/2;
                    let top = (h*dragonScale-bubbleSize/2);
                    console.log('%c left, top, bubbleSize, bubbleSize =========>', 'color:#722ed1;', left, top, bubbleSize, bubbleSize)
                    ctx2.drawImage(this, left, top, bubbleSize, bubbleSize);//this即是imgObj,保持图片的原始大小：470*480
                      }
          }
      }
    }

  }

  const createDiv = () => {
    //  div太占地方了
    let canvas = document.getElementById("canvas");
    let image = document.getElementById("image");
    var ctx = canvas.getContext("2d");
    var imageData = ctx.getImageData(0,0,image.width,image.height).data; 
    var dragonContainer = document.getElementById("container");
    var dragonScale = 4;

    for (var h = 0; h < image.height; h+=gap) {
        for(var w = 0; w < image.width; w+=gap){
                var position = (image.width * h + w) * 4;
                var r = imageData[position], g = imageData[position + 1], b = imageData[position + 2];

                if(r+g+b==0){
                        var bubble = document.createElement("div");
                        bubble.setAttribute("class","bubble");
                        var bubbleSize = Math.random()*10+20;
                        bubble.style.left = (w*dragonScale-bubbleSize/2) + "px";
                        bubble.style.top = (h*dragonScale-bubbleSize/2) + "px";
                        bubble.style.width = bubble.style.height = bubbleSize+"px";
                        bubble.style.animationDuration = Math.random()*4 + 2 + "s";
                        const img = document.createElement("img");
                        img.setAttribute("class","img");
                        let imgUrl = getRandomImage()
                        img.onclick = (e) => {
                          andToBodyPop(imgUrl)
                        }
                        img.src = imgUrl;
                        bubble.appendChild(img)
                        dragonContainer.appendChild(bubble);
                    }
        }
    }
  }

  const andToBodyPop = (imgSrc) => {
    setPopImg(imgSrc)
    console.log('%c imgSrc =========>', 'color:#722ed1;', imgSrc)
  }

  return (
    <div>
      <button onClick={handleGetdianzheng}> 获取点阵</button>
      <button onClick={createCanVusPaoPao}> canvus paopaolong</button>
      <button onClick={createDiv}>创建泡泡龙</button>
      <img id="image" src={dragon}></img>
      <canvas id="canvas"></canvas>
      <br/>
      <div id="container"></div>
      <canvas id="drawCanvus" width="1200" height="1200"></canvas>
      {
        <Modal show={!!popImg}>
          <div className="delete-icon" onClick={() => setPopImg('')}>
            ×
          </div>
          <img className="pop-img" src={popImg}></img>
        </Modal>
      }
      
    </div>
  )
}

export default () => (
  <div>
    <div>泡泡龙</div>
    <Draw/>
  </div>
)