import {init} from '@rematch/core'
import produce from 'immer' 
import rootSaga from './saga'
import createSagaMiddleware from 'redux-saga'
const sagaMiddleware = createSagaMiddleware()

let changeColor = (state, payload) => {
  state.color = payload
}

const models = {
  count: {
    state: {
      count: 0,
      color: 'red',
    },
    reducers: {
      increment(state, payload) {
        console.log('%c incrementpayload =========>', 'color:#722ed1;', state)
        return {
          ...state,
          count: state.count + payload
        }
      },
      changeColor: produce((state, payload) => {
        console.log('%c state =========>', 'color:#722ed1;', state)
        console.log('%c models =========>', 'color:#722ed1;', models.count)
        changeColor(state, payload)
      }),  //  produce(func)就是生成一个函数，调用这个函数返回一个值
      // 将一个state改变，然后返回改变后的newState， 但又不改变旧的state
      sagaAdd(state, payload) {
        console.log('%c sagaAdd =========>', 'color:#722ed1;')
        return {
          ...state,
        }
      },
    },
    effects: {
      async incrementAsync(payload, rootState) {
        await new Promise(resolve => setTimeout(resolve, 1000))
        this.increment(payload)
      }
    }
  }
}

const store = init({
  models,
  redux: {
    middlewares: [sagaMiddleware]
  }
})

sagaMiddleware.run(rootSaga)
export default store