import React from 'react'
import { Route, Switch, Redirect } from 'react-router-dom'
import { getFirstRouteFromModule } from './getFirstRoute'
import _ from 'lodash'


function SwitchRoutes({menus, routes}) {
  let redirectRoute = null
  if (menus && menus.length > 0) {
    redirectRoute = getFirstRouteFromModule({ menus })
  }
  console.log('%c redirectRoute =========>', 'color:#722ed1;', redirectRoute)
  console.log('%c routes =========>', 'color:#722ed1;', routes)
  return (
    <Switch>
      {routes.map(route => (
        <Route key={route.path} {...route} />
      ))}
      {redirectRoute && <Redirect to={redirectRoute.path} />}
    </Switch>
  )
}

function switchRoutes({menus, routes}) {
  function SwitchRoutes() {
    let redirectRoute = null
    if (menus && menus.length > 0) {
      redirectRoute = getFirstRouteFromModule({ menus })
    }
    console.log('%c redirectRoute =========>', 'color:#722ed1;', redirectRoute)
    console.log('%c routes =========>', 'color:#722ed1;', routes)
    return (
      <Switch>
        {routes.map(route => (
          <Route key={route.path} {...route} />
        ))}
        {redirectRoute && <Redirect to={redirectRoute.path} />}
      </Switch>
    )
  }
  return SwitchRoutes
}

// export default SwitchRoutes
export default switchRoutes
