import { useCallback, useEffect, useRef } from 'react'


//  大致感受是， 返回onMouseDown方法，onMouseDown绑定到某一个dom上， 此时再监听body的mouseMove, 触发一系列的操作
// /**
//  * 
//  * @param {*} param0 
//  * @returns {}
//  */
// 我仿写pascal的， 
// 由此可见，diyhook 是将一段逻辑拆出来而已，可以看成是没有dom的组件
const useMyMouseMove = ({ stopBodySelect = true, mouseDown, mouseMove, mouseUp }) => {
  const pRef = useRef({
    ox: 0,
    oy: 0,
    isMouseDown: false,
  })
  const getEventData = () => pRef.current
  const onMouseDown = ((e) => {
    console.log('%c wc onMouseDown =========>', 'color:#722ed1;', )
    let data = getEventData()
    data.oy = e.clientY;
    data.ox = e.clientX;
    data.isMouseDown = true
    if (stopBodySelect) {
      document.body.style.userSelect = 'none'
    }
    mouseDown({
      getEventData,
    })
  })
  // const onMouseMove = (e) => {
  //   let data = getEventData()
  //   // console.log('%c onMouseMove =========>', 'color:#722ed1;', data.isMouseDown)
  //   if (data.isMouseDown) {
  //     let dx = e.clientX - data.ox
  //     let dy = e.clientY - data.oy
  //     mouseMove({
  //       dx,
  //       dy,
  //       getEventData,
  //     })
  //   }
  // }
  //  usecallback还可以用在高频操作中，如果render很频繁的话，就要一直定义一个函数，但是如果usecallback就避免了这种多次定义
  const onMouseMove = useCallback((e) => {
    let data = getEventData()
    // console.log('%c onMouseMove =========>', 'color:#722ed1;', data.isMouseDown)
    if (data.isMouseDown) {
      let dx = e.clientX - data.ox
      let dy = e.clientY - data.oy
      mouseMove({
        dx,
        dy,
        getEventData,
      })
    }
  }, [])
  const onMouseUp = ((e) => {
    let data = getEventData()
    // console.log('%c onMouseUp =========>', 'color:#722ed1;', )
    data.isMouseDown = false
    if (stopBodySelect) {
      document.body.style.userSelect = 'auto'
    }
  })
  //  
  useEffect(() => {
    document.addEventListener('mousemove', onMouseMove)
    document.addEventListener('mouseup', onMouseUp)
    return () => {
      document.removeEventListener('mousemove', onMouseMove)
      document.removeEventListener('mouseup', onMouseUp)
    }
  }, [])
  return {
    onMouseDown,
  }
}
export default useMyMouseMove
